import styleInject from '#style-inject';styleInject(".fire-wrapper{width:100%;margin:0 auto;position:relative;display:inline-block;padding:40px 60px}.fire-title{text-transform:uppercase;text-align:center;color:#fff3;background:url(../img/fire-burn.jpg) repeat-x;background-size:contain;-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent;animation:fire 15s linear infinite;position:relative;z-index:2;margin:0;font-weight:700;filter:drop-shadow(0 0 20px rgba(255,107,0,.8)) drop-shadow(0 0 40px rgba(255,69,0,.6)) drop-shadow(0 0 60px rgba(255,69,0,.4))}.fire-glow-bottom{position:absolute;bottom:-20px;left:50%;transform:translate(-50%);width:90%;height:80px;background:radial-gradient(ellipse at center,rgba(255,165,0,.6) 0%,rgba(255,107,0,.5) 20%,rgba(255,69,0,.4) 40%,rgba(255,0,0,.2) 60%,transparent 80%);border-radius:50%;animation:fire-glow-pulse 2s ease-in-out infinite;z-index:0;pointer-events:none;filter:blur(15px)}.fire-glow-top{position:absolute;top:-20px;left:50%;transform:translate(-50%);width:80%;height:60px;background:radial-gradient(ellipse at center,rgba(255,165,0,.3) 0%,rgba(255,107,0,.2) 30%,transparent 70%);border-radius:50%;animation:fire-glow-pulse-top 3s ease-in-out infinite;z-index:0;pointer-events:none;filter:blur(20px)}.fire-particle{position:absolute;width:6px;height:6px;background:radial-gradient(circle,rgba(255,255,255,.9) 0%,rgba(255,235,59,.8) 20%,rgba(255,152,0,.6) 40%,transparent 70%);border-radius:50%;pointer-events:none;opacity:0;filter:blur(1px);box-shadow:0 0 10px #ffa500cc,0 0 20px #ff6b0099}.fire-particle:nth-child(3){animation:flame-rise 1.5s ease-in infinite;animation-delay:0s}.fire-particle:nth-child(4){animation:flame-rise 1.8s ease-in infinite;animation-delay:.3s}.fire-particle:nth-child(5){animation:flame-rise 1.6s ease-in infinite;animation-delay:.6s}.fire-particle:nth-child(6){animation:flame-rise 1.7s ease-in infinite;animation-delay:.9s}.fire-particle:nth-child(7){animation:flame-rise 1.9s ease-in infinite;animation-delay:1.2s}.fire-particle:nth-child(8){animation:flame-rise 1.4s ease-in infinite;animation-delay:.2s}.fire-particle:nth-child(9){animation:flame-rise 2s ease-in infinite;animation-delay:.5s}.fire-particle:nth-child(10){animation:flame-rise 1.6s ease-in infinite;animation-delay:.8s}.fire-particle:nth-child(11){animation:flame-rise 1.7s ease-in infinite;animation-delay:1.1s}.fire-particle:nth-child(12){animation:flame-rise 1.5s ease-in infinite;animation-delay:.4s}.fire-particle:nth-child(13){animation:flame-rise 1.8s ease-in infinite;animation-delay:.7s}.fire-particle:nth-child(14){animation:flame-rise 1.9s ease-in infinite;animation-delay:1s}@keyframes fire{0%{background-position:left 0 top 25px}50%{background-position:left 130px top -35px}to{background-position:left 250px top 0px}}@keyframes fire-glow-pulse{0%,to{opacity:.8;transform:translate(-50%) scale(1)}50%{opacity:1;transform:translate(-50%) scale(1.1)}}@keyframes fire-glow-pulse-top{0%,to{opacity:.6;transform:translate(-50%) scale(1)}50%{opacity:.9;transform:translate(-50%) scale(1.15)}}@keyframes flame-rise{0%{bottom:0%;left:calc(var(--x-pos) * 1%);opacity:0;transform:scale(.5) rotate(0);background:radial-gradient(circle,rgba(255,255,255,.9) 0%,rgba(255,235,59,.8) 30%,transparent 70%)}20%{opacity:1;transform:scale(1) rotate(45deg)}50%{opacity:.8;transform:scale(1.2) rotate(90deg);background:radial-gradient(circle,rgba(255,235,59,.8) 0%,rgba(255,152,0,.7) 40%,transparent 70%)}70%{opacity:.4;transform:scale(.8) rotate(135deg)}to{bottom:100%;left:calc((var(--x-pos) + (var(--x-drift) * 15)) * 1%);opacity:0;transform:scale(.3) rotate(180deg);background:radial-gradient(circle,rgba(255,87,34,.5) 0%,transparent 60%)}}\n")