import styleInject from '#style-inject';styleInject(".birthday-text-container{display:inline-block;position:relative}.birthday-text{display:inline-block;font-weight:700;position:relative;z-index:2}.birthday-char{display:inline-block;background:linear-gradient(45deg,#ff6b9d,#c06c84,#f67280,#355c7d,#6c5ce7);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-size:300% 300%;animation:birthday-rainbow 3s ease-in-out infinite,birthday-bounce .6s ease-in-out infinite;text-shadow:2px 2px 4px rgba(255,107,157,.5),-2px -2px 4px rgba(108,92,231,.5)}.birthday-confetti{position:absolute;top:-50%;left:0;width:100%;height:200%;pointer-events:none;overflow:hidden}.confetti{position:absolute;width:10px;height:10px;border-radius:50%;animation:birthday-fall 3s linear infinite;opacity:.8}@keyframes birthday-rainbow{0%,to{background-position:0% 50%}50%{background-position:100% 50%}}@keyframes birthday-bounce{0%,to{transform:translateY(0) rotate(0)}50%{transform:translateY(-10px) rotate(5deg)}}@keyframes birthday-fall{0%{transform:translateY(-100%) rotate(0);opacity:1}to{transform:translateY(200%) rotate(720deg);opacity:0}}\n")