import styleInject from '#style-inject';styleInject("@import\"https://api.fontshare.com/v2/css?f[]=satoshi@901&display=swap\";.freestyle-container{display:inline-block;width:100%;max-width:100%;user-select:none}.freestyle-svg{width:100%;height:auto;display:block}.freestyle-text{font-family:Satoshi,sans-serif;font-weight:900;text-transform:uppercase}@keyframes freestyle-bounce{0%{transform:translateY(0)}to{transform:translateY(-20px)}}@keyframes freestyle-wave{0%,to{transform:translateY(0) scaleY(1)}50%{transform:translateY(-15px) scaleY(1.1)}}@keyframes freestyle-color-pulse{0%,to{opacity:1}50%{opacity:.7}}.freestyle-svg rect:nth-child(odd){animation:freestyle-bounce 1.2s ease-in-out infinite alternate!important}.freestyle-svg rect:nth-child(2n){animation:freestyle-wave 1.5s ease-in-out infinite alternate!important}.freestyle-svg rect{animation-timing-function:ease-in-out}\n")